// RFS (Responsive Font Size) stub
// This is a minimal implementation for compatibility
// RFS is a Bootstrap utility for responsive typography

@mixin rfs($values, $property: font-size) {
  $val: $values;
  @if type-of($values) == list {
    $val: nth($values, 1);
  }
  #{$property}: $val;
  
  @if $val != 0 and type-of($val) == number {
    @if unit($val) != "px" {
      @media (min-width: 1200px) {
        #{$property}: $val;
      }
    } @else {
      @media (min-width: 1200px) {
        #{$property}: calc(#{$val} + 0.5vw);
      }
    }
  }
}

@mixin font-size($value) {
  @include rfs($value, font-size);
}

@function rfs-value($values) {
  @return $values;
}

@function rfs-fluid-value($values) {
  @return $values;
}

